*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App_container {
  background-color: #E9A508;
  border-radius: .5rem;
  min-width: 50%;
  min-height: 40vh;
  padding: .5rem;
}
.container_CityCountry{
  width: 100%;
  text-align: center;
  color: #E9084C;
  font-weight: bold;
}
.container_IconButtonExtra{
  display: flex;
  width: 100%;
  min-height: 30vh;
  padding: .5rem;
}
.IconButtonExtra_IconButton{
  width: 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
}
.IconButton_Tem{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  padding: .5rem;
}
.IconButton_Tem p{
  font-size: 3rem;
}
.IconButtonExtra_IconButton button{
  border: none;
  background-color: #C8143E;
  padding: .5rem 2rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: .3rem;
  color: #ffffff;
}
.IconButtonExtra_IconButton button:hover{
  cursor: pointer;
}
.IconButtonExtra_Extra{
  width: 55%;
  padding-left: .5rem;
  background-color: #ffffff;
}
.IconButtonExtra_Extra div {
  padding-top: 2rem;
}
.IconButtonExtra_Extra div p{
  color: rgb(87, 87, 87);
  font-weight: bold;
}
.IconButtonExtra_Extra div p span{
  font-size: 1.1rem;
  color: #000000;
}

/*MEDIA QUERI*/
@media (max-width: 768px){
  .container_IconButtonExtra{
    display: flex;
    flex-direction: column;
  }
  .IconButtonExtra_IconButton{
    width: 100%;
    padding-bottom: .3rem;
  }
  .IconButtonExtra_Extra{
    width: 100%;
    margin-top: .2rem;
  }
}
